.linkWrapper {
  color: inherit;
  text-decoration: none;
  text-align: left;
  font-size: 1.1rem;
}

.linkWrapper:hover {
  color: var(--color-secondary);
}

.icon {
  width: 50px;
}

.linkBody {
  margin: 1em 2em;
  display: grid;
  grid-template-columns: 1fr 3fr;
}

.linkText {
  margin-left: 1em;
}